    top: 0;
    left: 50%;
    margin-left: -960px;
    width: 1920px;
    height: 600px;
}
.ivu-carousel:hover >>> .ivu-carousel-arrow-hover {
    display: none;
}
.ivu-carousel >>> .ivu-carousel-list {
    height: 600px;
}
.ivu-carousel >>> .ivu-carousel-dots {
    display: inline-block;
    width: auto;
    height: 24px;
    padding: 6px 10px;
    background: rgba(255,255,255,0.5);
    border-radius: 18px;
    margin-top: 35px;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%); 
}
.ivu-carousel >>> .ivu-carousel-dots li {
    margin: 0 3px;
    padding: 0;
}
.ivu-carousel >>> .ivu-carousel-dots li button {
    opacity: 1;
    width: 12px;
    height: 12px;
    background:rgba(125,125,125,1);
    border-radius: 6px;
}
.ivu-carousel >>> .ivu-carousel-dots li.ivu-carousel-active > button {
    width: 29px;
    background:rgba(200,49,60,1);
}

/* --- 内容部分 --- */
.main-content {
    width: 100%;
    background: #fff;
}
.container-wrapper {
	margin-top: -160px;
	z-index: 1;
	position: relative;
	width: 100%;
	padding: 0 20px;
	background: #fff;
	background: -webkit-linear-gradient(90deg,#ffffff 95%,#e0e0de 99%);
	border-radius:5px;  
}
.row-area {
	display: flex;
	justify-content: space-between;
}
.row-area .left-column {
	flex: 0 0 285px;
	width: 285px;
	position: relative;
	overflow: hidden;
}
.row-area .right-column {
	flex: 0 0 860px;
	width: 860px;
}
.public-title {
	padding: 15px 0 15px 15px;
}
/* --- 平台公告 --- */
.platform-notice .more {
	float: right;
	margin-top: 4px;
	line-height: 24px;
	font-size: 14px;
	font-weight: 200;
	color: #2d8cf0;
	border: 1px solid #2d8cf0;
	border-radius: 5px;
	padding: 0 5px;
}
.hot-news {
	padding-bottom: 10px;
	border-bottom: 1px solid #eee;
    overflow: hidden;
}
.hot-news h4 {
	padding-bottom: 5px;
	line-height: 25px;
	font-size: 16px;
	color: #f3596a;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}
.hot-news img {
	float: left;
	width: 84px;
	height: 62px;
}
.hot-news .hot-news-info {
	margin-left: 100px;
	height: 62px;
}
.hot-news .hot-news-info p {
	height: 40px;
	line-height: 20px;
	color: #999;
	overflow: hidden;
	word-wrap: break-word;
	text-overflow: ellipsis;
	display: -webkit-box;
	-webkit-line-clamp: 2;
	/*! autoprefixer: off */  
	-webkit-box-orient: vertical;  
	/* autoprefixer: on */
}
.hot-news .hot-news-info .detail {
	display: inline-block;
	line-height: 22px;
	color: #2d8cf0;
}
.hot-news:hover {
  	cursor: pointer;
}
.hot-news:hover h4 {
  	color: #2d8cf0;
}
.hot-news:hover p {
  	color: #495060;
}
.notice-list {
	height: 120px;
	overflow: hidden;
}
.notice-list li {
	line-height: 30px;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}
.notice-list li:hover {
	cursor: pointer;
	color: #2d8cf0;
}
.no-notice {
	text-align: center;
	height: 224px;
	line-height: 224px;
}
.no-notice img {
	vertical-align: middle;
	width: 120px;
}
/* --- 投注动态 --- */
.bet-dynamic-wrapper {
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	flex: 1;
	display: flex;
	flex-direction: column;
}
.bet-dynamic {
	flex: 1;
	padding: 20px;
	border: 1px solid #eee;
	border-radius: 5px;
	overflow: hidden;
}
.bet-dynamic ul {
  	animation: loopSlide 15s linear infinite;
}
@keyframes loopSlide {
	0%   { transform: translateY(0%); }
	100% { transform: translateY(-50%); }
}
.bet-dynamic ul li {
	width: 100%;
	line-height: 30px;
	font-size: 14px;
	color: #222;
	border-bottom: 1px solid #eee;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}
.text-blue {
  	color: #2d8cf0;
}
.text-red {
  	color: red;
}
/* --- 彩种列表 --- */
.lottery-list {
	margin-top: -8px;
	position: relative;
	overflow: hidden;
	padding: 8px 5px 3px 5px;
}
.lottery-list ul {
  	margin-top: -20px;
}
.lottery-list li {
	float: left;
	margin-top: 20px;
	margin-right: 20px;
	position: relative;
	width: 270px;
	height: 114px;
	cursor: pointer;
	background-color: #ffffff;
	border: solid 1px #eeeeee;
	border-radius: 5px;
	box-shadow: 1px 1px 5px 0px rgb(187, 187, 187);
	overflow: hidden;
	transition: all 0.2s ease-in-out;
}
.lottery-list li:nth-child(3n) {
  	margin-right: 0
}
.lottery-list li .today-draw {
	z-index: 1;
	position: absolute;
	top: 0;
	left: 0;
}
.lottery-list li .shadow {
	z-index: 2;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	opacity: 0;
	transition: opacity .5s;
	background: rgba(0, 0, 0, 0.1);
}
.lottery-list li .period-info {
	z-index: 1;
	position: absolute;
	right: 0px;
	padding: 0 5px;
	color: #FFF;
	font-size: 16px;
	background: rgba(0, 0,0,0.1);
	border-radius:5px 0px 0px 5px; 
}
.lottery-list li .lottery-img {
	width: 270px;
	height: 114px;
}
.lottery-list li:hover {
	transform:translate(0, -8px);
	box-shadow: 0 1px 4px 1px #bbb;
}
.lottery-list li:hover .shadow {
  	opacity: 1
}
.hot-lottery .lottery-list li {
  	height: 224px;
}
.hot-lottery .lottery-list li .lottery-img {
	width: 270px;
	height: 224px;
}
.hot-lottery .go-game {
	z-index: 5;
	position: absolute;
	bottom: 4px;
	right: 4px;
	padding: 3px 10px;
	color: #fff;
	border: 1px solid #eee;
	border-radius: 10px;
}
.hot-lottery li:hover .go-game {
	background: #f3596a;
	color: white;
}

/* --- 快捷通道 --- */
.fast-channel {
	border: 1px solid #eee;
	border-radius: 5px;
}
.fast-channel >>> .ivu-col a {
	display: block;
	text-align: center;
	padding: 8px 0;
}
.fast-channel >>> .ivu-col a:hover {
	background: rgba(0,0,0,.01)
}
.fast-channel >>> .ivu-col:nth-of-type(1) {
	border-right: 1px solid #eee;
	border-bottom: 1px solid #eee;
}
.fast-channel >>> .ivu-col:nth-of-type(2) {
  	border-bottom: 1px solid #eee;
}
.fast-channel >>> .ivu-col:nth-of-type(3) {
 	border-right: 1px solid #eee;
}
.fast-channel >>> .ivu-col .icon {
	display: inline-block;
	width: 24px;
	height: 24px;
	background-repeat: no-repeat;
	background-position: center; 
}
.fast-channel >>> .ivu-col p {
	padding-top: 5px;
	line-height: 1;
}

.fast-channel >>> .ivu-col:nth-of-type(1) p {
  	color: #63D0CA;
}
.fast-channel >>> .ivu-col:nth-of-type(2) p {
  	color: #0095EA;
}
.fast-channel >>> .ivu-col:nth-of-type(3) p {
	color: #E8AF56;
}
.fast-channel >>> .ivu-col:nth-of-type(4) p {
  	color: #8EE856;
}
/* --- 平台数据 --- */
.platform-data {
	padding: 20px 0;
	border: 1px solid #eee;
	border-top-width: 6px;
	border-top-color: #F3596A;
}
.platform-data >>> .ivu-col {
  	text-align: center;
}
.platform-data >>> .ivu-col:nth-of-type(2) {
	border-left: 2px dotted rgba(153, 153, 153, .4);
	border-right: 2px dotted rgba(153, 153, 153, .4);
}
.platform-data .icon {
	display: inline-block;
	margin-right: 4px;
	vertical-align: top;
	width: 24px;
	height: 24px;
	background-repeat: no-repeat;
	background-position: center; 
}
.platform-data >>> .ivu-col p {
	color: #48484C;
	font-size: 16px;
	line-height: 24px;
}
.platform-data >>> .ivu-col h4 {
	font-size: 32px;
	font-weight: 700;
	line-height: 48px;
	font-family: Georgia, "Times New Roman", Times, serif;
}
.platform-data .ingot h4 {
  	color: #E64254;
}
.platform-data .rate h4 {
  	color: #F48D12;
}
.platform-data .enrollment h4 {
  	color: #4A90E2;
}
/* --- 底部下载 --- */
.download-area {
	margin-top: 10px;
	margin-bottom: 10px;
	position: relative;
    width: 100%;
	height: 152px;
	background: url(../../assets/images/appxiazai.png) no-repeat;
    background-size: 100%;
}
.download-area .imgapp {
	position: absolute;
	left: 29px;
	top: 29px;
	width: 87px;
	height: 87px;
}

/*--- 红包模块 ---*/
.redPacket {
    z-index: 1;
    position: absolute;
    top: 60px;
    left: -95px;
    width: 109px;
    height: 287px;
    cursor: pointer;
}
.redPacket span {
    position: absolute;
    top: 12px;
    left: 44px;
    width: 46px;
    height: 207px;
    animation: twinkling 1.5s infinite ease-in-out;
}
/* .redPacket .text2 {
    z-index: 1;
    background: url('../../assets/images/red/text2.png') no-repeat;
    animation-name: twinkling2;
} */
@keyframes twinkling {
    0%   { transform:scale(1,1); }
    20%  { transform:scale(1.02,1.02); }
    30%  { transform:scale(1.03,1.03); }
    40%  { transform:scale(1.04,1.04); }
    50%  { transform:scale(1.05,1.05); }
    60%  { transform:scale(1.04,1.04); }
    70%  { transform:scale(1.03,1.03); }
    80%  { transform:scale(1.02,1.02); }
    100% { transform:scale(1,1); }
}
@keyframes twinkling2 {
    0%   { opacity:1; transform: scale(1); }
    100% { opacity:0; transform: scale(1.05); }
}
.red-modal >>> .ivu-modal-close .ivu-icon-ios-close-empty,
.red-modal >>> .ivu-modal-close .ivu-icon-ios-close-empty:hover {
    z-index: 1;
    color: #fff;
}
.red-modal >>> .ivu-modal-content {
    background: none;
    border-radius: 12px;
}
.red-modal >>> .ivu-modal-body {
    padding: 0;
}
.red-modal >>> .ivu-modal-footer {
    display: none;
}
.red-modal .red-wrap {
    position: relative;
    min-height: 400px;
    border-radius: 12px;
}
.red-modal .red-notOpen {
    width: 342px;
    height: 480px;
    padding-top: 220px;
    text-align: center;
}
.red-wrap .red-avartar {
    display: inline-block;
    width: 96px;
    height: 96px;
    border: 5px solid #f2261f;
    border-radius: 50%;
    box-shadow: 0px 1px 2px rgba(0,0,0,.2);
}
.red-wrap .red-avartar img  {
    width: 86px;
    height: 86px;
    border-radius: 50%;
}
.red-notOpen .red-avartar {
    position: absolute;
    top: 78px;
    left: 50%;
    margin-left: -48px;
}
.red-notOpen .red-slogan {
    width: 100%;
    height: 40px;
    line-height: 40px;
    font-size: 32px;
    color: #ffff00;
}
.red-notOpen .red-btn {
    margin-top: 50px;
}
.red-notOpen .red-btn a {
    position: relative;
    display: inline-block;
    width: 110px;
    height: 110px;
    cursor: pointer;
}
.red-notOpen .red-btn .openAnimation .open-btn {
    animation: turn 2s infinite;
}
.red-notOpen .red-btn .open-btn {
    position: absolute;
    top: 0;
    left: 0;
    width: 110px;
    height: 110px;
    border-radius: 50%;
    box-shadow: 8px 8px 0 rgba(0,0,0,.1);
}
.red-notOpen .red-btn .front {
    z-index: 5;
    backface-visibility: hidden;
}
@keyframes turn {
    to { transform: rotateY(360deg); }
}
.red-modal .red-opened {
    width: 400px;
    text-align: center;
    color: #333;
}
.red-opened .red-title {
    padding: 20px 0;
    font-size: 24px;
    color: #fff;
    font-weight: 400;
    line-height: 1;
}
.red-opened .red-info {
    padding: 5px 0;
}
.red-opened .red-info p {
    font-size: 22px;
    line-height: 30px;
}
.red-opened .red-info > p {
    position: relative;
    padding: 0 110px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.red-opened .red-info .best-luck {
    position: absolute;
    right: 30px;
    font-size: 14px;
}
.red-opened .red-info .best-luck .icon-luck {
    float: left;
    margin-top: 7px;
    margin-right: 5px;
    width: 18px;
    height: 14px;
}
.red-opened .red-info .money-info {
    padding-top: 10px;
}
.red-opened .red-info .money-info h4 {
    font-size: 40px;
    line-height: 1;
}
.red-opened .red-info .money-info p {
    font-size: 13px;
    color: #4375db;
}
.red-opened .red-info .finished {
    padding: 30px 0;
    font-size: 18px;
    font-weight: 400;
    line-height: 1.2;
}
.red-opened .red-winner {
    background: #fff;
    height: auto;
    max-height: 260px;
    overflow-y: scroll;
}
#redWinner::-webkit-scrollbar {
	width: 8px;
	background-color: #F6F6F6;
}
#redWinner::-webkit-scrollbar-track {
    background-color: #F6F6F6;
    border-radius: 4px;
}
#redWinner::-webkit-scrollbar-thumb {
	border-radius: 4px;
    background-color: #E6E6E6;
}
#redWinner::-webkit-scrollbar-thumb:hover {
    background-color: #ccc;
}
.red-opened .red-winner dl {
    padding: 10px 20px 10px 20px;
    border-bottom: 1px solid #eee;
}
.red-opened .red-winner dl dt,
.red-opened .red-winner dl dt img {
    float: left;
    width: 44px;
    height: 44px;
    border-radius: 50%;
}
.red-opened .red-winner dl dd {
    position: relative;
    padding-left: 60px;
    padding-right: 100px;
    text-align: left;
}
.red-opened .red-winner dl dd p {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.red-opened .red-winner dl dd p:nth-of-type(1) {
    font-size: 18px;
    line-height: 24px;
}
.red-opened .red-winner dl dd p:nth-of-type(2) {
    font-size: 16px;
    color: #999;
    line-height: 20px;
}
.red-opened .red-winner dl dd .red-money {
    position: absolute;
    top: 0;
    right: 0;
    font-size: 20px;
    line-height: 44px;
}
.red-opened .red-record {
    padding: 15px 0;
    line-height: 24px;
    font-size: 16px;
    background: #fff;
    border-radius: 0 0 12px 12px;
}
.red-rule-modal >>> .ivu-modal-footer {
    display: none;
}
.red-rule-modal >>> .ivu-modal-header-inner {
    font-size: 18px;
    font-weight: 400;
}
.red-rule-modal >>> .ivu-modal-body {
    padding: 10px 20px 20px;
}
.red-rule-modal .rule-content {
    font-size: 14px;
}
.red-rule-modal .rule-content ul {
    width: 100%;
    height: auto;
    overflow: hidden;
}
.red-rule-modal .rule-content h5 {
    padding: 20px 0 10px 0;
    font-size: 16px;
    font-weight: 400;
    line-height: 1;
    border-bottom: 1px solid #eaeaea;
}
.red-rule-modal .rule-content ul li {
    float: left;
    position: relative;
    padding: 5px 0;
    width: 100%;
    height: auto;
    line-height: 24px;
    border-bottom: 1px solid #eaeaea;
}
.red-rule-modal .rule-content ul li .request {
    position: absolute;
    top: 50%;
    right: 0;
    margin-top: -12px;
    line-height: 25px;
}
.red-rule-modal .rule-content ul li .request .icon-yes {
    float: left;
    margin-top: 5px;
    margin-right: 5px;
    width: 15px;
    height: 15px;
}
.red-rule-modal .rule-content p {
    padding-top: 10px;
    color: #999;
}
</style>



